import Vue from "vue";
import Router from "vue-router";

import Index from "./components/Index.vue";
import New from "./components/New.vue";
import Product from "./components/Product.vue";
import Search from "./components/Search.vue";
import Reward from './components/Reward.vue';
import Reward1 from './components/Reward1.vue';

Vue.use(Router);

const router = new Router({
  mode: "history",
  routes: [
    {
      path: "/web",
      component: Index,
      meta: { text: "Menu" },
      children: [
        {
          path: "/",
          component: Product,
          name: "product",
        },
        {
          path: "/web/category/:key",
          component: Search,
          name: "search",
        },
        {
          path: "/web/reorder",
          component: New,
          name: "new",
        },
        {
          path: "/web/reward",
          component: Reward,
          name: "reward",
        },
        {
          path: "/web/reward1",
          component: Reward1,
          name: "reward1",
        },
      ],
    },
  ],
});

export default router;
