<template>
  <div class="menu-pro" id="menu">
    <div class="item-pro" v-for="element in data" :key="element.key">
      <div class="wrap-img">
        <img :src="element.ImgUrl" />
      </div>
      <div class="info">
        <p class="name-pro">{{ element.name }}</p>
        <div class="flex-container">
          <div>
            <span class="title-price">Giá dự kiến</span>
            <p class="price-pro">
              {{ formatPrice(element.price) }}
              <span class="vnd-set"> VND </span>
            </p>
          </div>
          <div class="mini-container">
            <p :class="element.status == false ? `end-slot` : ``">
              Slot: <span> {{ element.status == true ? `Còn` : "Hết" }} </span>
            </p>
            <p class="mini-price">
              Cọc:
              <span>
                {{ formatPrice(element.deposit)
                }}<span class="vnd-set-mini"> VND </span>
              </span>
            </p>
          </div>
        </div>
        <div>
          <p class="month">Dự kiến hàng về: {{ element.date }}</p>
        </div>
      </div>
      <a href="#" class=""></a>
    </div>
  </div>
</template>

<script>
import Gundam from "../services/Gundam";
import Title from "../services/Title";
export default {
  data() {
    return {
      data: [],
    };
  },
  methods: {
    onTitleChange(items) {
      let _title = [];

      items.forEach((item) => {
        let data = item.val();
        _title.push({
          mess: data,
        });
      });

      console.log(_title);

      this.$store.dispatch("title", _title[0].mess);
    },
    onDataChange(items) {
      let _tutorials = [];

      items.forEach((item) => {
        let data = item.val();
        if (data.status === true) {
          _tutorials.push({
            ImgUrl: data.ImgUrl,
            deposit: data.deposit,
            id: data.id,
            name: data.name,
            price: data.price,
            status: data.status,
            date: data.date,
          });
        }
      });

      this.data = _tutorials;
    },
    getAll() {
      Gundam.getAll().on("value", this.onDataChange);
      Title.getAll().on("value", this.onTitleChange);
    },
    formatPrice(value) {
      let val = (value / 1).toFixed(0).replace(".", ",");
      return val.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ".");
    },
  },
  mounted() {
    this.getAll();
  },
};
</script>

<style scoped>
.month {
  color: #efc83d;
  font-size: 16px;
  font-weight: 500;
}
</style>
