import Vue from 'vue'
import Vuex from 'vuex';

Vue.use(Vuex);

export const store = new Vuex.Store({
    state: {
        title: null,
        searchName: null,
    },
    getters: {
        title: (state) => {
            return state.title || localStorage.getItem('title');
        },
        searchName: (state) => {
            return state.searchName || localStorage.getItem('searchName');
        }
    },
    actions: {
        title(context, title) {
            context.commit('title', title);
        },
        searchName(context, searchName) {
            context.commit('searchName', searchName);
        },
    },
    mutations: {
        title(state, title) {
            state.title = title;
        },
        searchName(state, searchName) {
            state.searchName = searchName;
        },
    }
});
