<template>
  <div class="menu-pro" id="menu">
    <div class="item-pro" v-for="element in data" :key="element.key">
      <div class="wrap-img">
        <img :src="element.ImgUrl" />
      </div>
      <div class="info">
        <p class="name-pro">{{ element.name }}</p>
        <div class="">
          <div>
            <span class="title-price">
              <p class="price-pro">
                Giá: {{ formatPrice(element.price) }}
                <span class="vnd-set"> VND </span>
              </p></span
            >
          </div>
          <div class="mini-container">
            <p>
              Loại:
              <span>
                {{ category.find((o) => o.key === element.category).content }}
              </span>
            </p>
          </div>
        </div>
      </div>
      <a href="#" class=""></a>
    </div>
  </div>
</template>

<script>
import Product from "../services/Product";
import Title from "../services/Title";
export default {
  data() {
    return {
      data: [],
      category: [
        {
          key: "mg",
          content: "1/100, MG, MEGA, PG",
        },
        {
          key: "rg",
          content: "RG BANDAI",
        },
        {
          key: "hg",
          content: "HG/EG BANDAI",
        },
        {
          key: "sd",
          content: "SD BANDAI",
        },
        {
          key: "ff",
          content: "Figure- figure rise",
        },
        {
          key: "30mm",
          content: "30MM/ 30MS",
        },
        {
          key: "kobu",
          content: "KotobukiYA",
        },
        {
          key: "pokemon",
          content: "POKEPLA",
        },
        {
          key: "candytoys",
          content: "candytoys, shokugan, orther",
        },
        {
          key: "base",
          content: "BASE, BUild customs",
        },
        {
          key: "tools",
          content: "Tools",
        },
      ],
    };
  },
  methods: {
    onDataChange(items) {
      let _tutorials = [];

      items.forEach((item) => {
        let data = item.val();
        if (data.status === true) {
          _tutorials.push({
            ImgUrl: data.ImgUrl,
            category: data.category,
            id: data.id,
            name: data.name,
            price: data.price,
            status: data.status,
          });
        }
      });

      this.data = _tutorials;

      console.log(this.data);
    },

    onTitleChange(items) {
      let _title = [];

      items.forEach((item) => {
        let data = item.val();
        _title.push({
          mess: data,
        });
      });

      console.log(_title);

      this.$store.dispatch("title", _title[1].mess);
    },

    formatPrice(value) {
      let val = (value / 1).toFixed(0).replace(".", ",");
      return val.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ".");
    },

    getAll() {
      Product.getAll().on("value", this.onDataChange);
      Title.getAll().on("value", this.onTitleChange);
    },
  },
  mounted() {
    this.getAll();
  },
};
</script>

<style></style>
