<template>
  <div class="container-reward">
    <div class="container-img">
      <img class="img-child" src="./../assets/reward/HUD1.png" alt="" />
      <!-- <img class="img-child" src="./../assets/reward/binhchon.png" alt="" />
      <img
        class="img-child"
        src="./../assets/reward/5- hoan thien.png"
        alt=""
      />
      <img class="img-child" src="./../assets/reward/6-son.png" alt="" />
      <img class="img-child" src="./../assets/reward/7 y tuong.png" alt="" />
      <img class="img-child" src="./../assets/reward/8 tieu de.png" alt="" />
      <img class="img-child" src="./../assets/reward/9-giai.png" alt="" />
      <img class="img-child" src="./../assets/reward/10-maso.gif" alt="" />
      <img class="img-child" src="./../assets/reward/11-tong-diem.gif" alt="" />
      <img class="img-child" src="./../assets/reward/khung.png" alt="" /> -->
    </div>

    <div class="container-data">
      <div class="point-container">
        <div class="container-data-flex top">
          <div class="prize-container">
            <h1 class="data-child prize">{{ this.record.name }}</h1>
          </div>
          <h1 class="data-child numerical-order">{{ this.record.code }}</h1>
          <h1 class="data-child total-score">{{ count1 }}</h1>
        </div>
        <!-- <div class="container-data-flex bottom">
          <h1 class="data-child orther-point-1">{{ count2 }}</h1>
          <h1 class="data-child orther-point-2">{{ count3 }}</h1>
          <h1 class="data-child orther-point-3">{{ count4 }}</h1>
        </div> -->
        <div class="vote">
          <h1>{{ count5 }}</h1>
          <div class="key-btn">
            <button class="btn" @click="check">Điểm</button>
            <button class="btn" @click="setImage">Hình</button>
            <button class="btn" @click="getByID(1)">1</button>
            <button class="btn" @click="getByID(2)">2</button>
            <button class="btn" @click="getByID(3)">3</button>
            <button class="btn" @click="getByID(4)">4</button>
            <button class="btn" @click="getByID(5)">5</button>
            <button class="btn" @click="getByID(6)">6</button>
          </div>
        </div>
      </div>
      <div class="img-container">
        <img class="img-child" :src="this.img" alt="" />
        <img
          class="img-child check"
          src="./../assets/reward/monitar.jpg"
          alt=""
        />
      </div>
    </div>
  </div>
</template>

<script>
import Reward from "./../services/Reward";
export default {
  data() {
    return {
      data: [],
      record: {
        name: 6,
      },

      num: 50,
      count1: 0,
      count2: 0,
      count3: 0,
      count4: 0,
      count5: 0,

      status: 0,
      holder: null,

      img: "./../assets/logo-shopee.jpg",
    };
  },
  methods: {
    onTitleChange() {
      let _title = "";
      this.$store.dispatch("title", _title);
    },
    setCount(val) {
      const obj = { n: 0 };
      this.holder = this.$anime({
        targets: obj,
        n: val,
        round: 1,
        duration: 5000,
        easing: "linear",
        loop: true,
        autoplay: false,
        update: () => {
          this.count1 = obj.n;
          this.count2 = obj.n;
          this.count3 = obj.n;
          this.count4 = obj.n;
          this.count5 = obj.n;
        },
      });
    },
    check() {
      this.status = this.status == 1 ? 0 : 1;
      if (this.status == 1) {
        this.holder.play();
      } else {
        this.holder.pause();
        this.count1 = this.record.point;
        this.count2 = this.record.condition;
        this.count3 = this.record.paint;
        this.count4 = this.record.idea;
        this.count5 = this.record.vote;
      }
    },

    setImage() {
      this.$anime({
        targets: ".img-container .check",
        opacity: 0,
        duration: 1000,
        easing: "linear",
        loop: false,
      });

      this.$anime({
        targets: ".numerical-order",
        opacity: 1,
        duration: 1000,
        easing: "linear",
        loop: false,
      });
    },

    getAll() {
      Reward.getAll().on("value", this.onDataChange);
    },
    onDataChange(items) {
      let _data = [];

      items.forEach((item) => {
        let data = item.val();
        _data.push({
          id: data.id,
          code: data.code,
          name: data.name,
          condition: data.condition,
          paint: data.paint,
          idea: data.idea,
          vote: data.vote,
          point: data.point,
          ImgUrl: data.ImgUrl,
          status: data.status,
        });
      });

      this.data = _data;
    },
    prepareDelete(key) {
      this.dialogDelete = true;
      this.deleteItem = key;
    },
    getByID(value) {
      this.$anime({
        targets: ".numerical-order",
        opacity: 0,
        duration: 0,
        easing: "linear",
        loop: false,
      });
      this.data.forEach((element) => {
        if (value == element.name) {
          this.record = element;
          this.img = element.ImgUrl;
        }
      });

      this.$anime({
        targets: ".img-container .check",
        opacity: 1,
        duration: 1,
        easing: "linear",
        loop: false,
      });
      this.count1 = 0;
      this.count2 = 0;
      this.count3 = 0;
      this.count4 = 0;
      this.count5 = 0;
      this.setCount(this.num);
    },
  },
  mounted() {
    this.onTitleChange();
    this.getAll();
    this.setCount(this.num);
  },
};
</script>

<style scoped>
@import url("https://fonts.googleapis.com/css2?family=Bungee+Inline&family=Manrope:wght@200;300;400;500;600;700;800&display=swap");
