  position: absolute;
  display: flex;
  width: 100%;
  min-height: 444px;
}

.point-container {
  flex-basis: 50%;
}
.img-container {
  flex-basis: 50%;
  margin-left: 24px;
  margin-top: 89px;
}

.img-container img {
  width: 458px;
  height: 302px;
}

.container-data-flex {
  display: flex;
  justify-content: space-around;
}

.data-child {
  color: #ffff;
  display: block;
  font-family: "Bungee Inline", cursive;
  font-family: "Manrope", sans-serif;
  text-align: center;
  width: 100px;
}

.point-container .top {
  height: 50%;
  align-items: end;
  padding-bottom: 43px;
  justify-content: end;
  padding-right: 95px;
}

.point-container .bottom {
  height: 27%;
  align-items: start;
  padding-top: 28px;
  text-align: center;
}

.prize {
  font-size: 120px;
  text-align: end;
  font-weight: 800;
}

.numerical-order {
  font-size: 60px;
  margin-right: 77px;
  opacity: 0;
}

.total-score {
  font-size: 60px;
}

