  margin-top: 50px;
}
.vote h1 {
  color: #ffff;
  display: block;
  text-align: end;
  padding-right: 234px;
  padding-top: 7px;
}

.prize-container {
  position: absolute;
  left: 35px;
  top: 70px;
}

