<template>
  <div class="reward1">
    <div style="display: none;">
      <el-carousel
        arrow="always"
        indicator-position="outside"
        type="card"
        height="500px"
      >
        <el-carousel-item
          v-for="value in data"
          :key="value.id"
          class="carousel-cell"
        >
          <img :src="value.ImgUrl" alt="" />
        </el-carousel-item>
      </el-carousel>
    </div>

    <div>
      <img src="./../assets/reward/new-bg.jpg" alt="">
    </div>

    <div class="container-reward">
      <div class="img-container">
        <img class="img-child" :src="this.reward2.ImgUrl" alt="" />
        <img
          class="img-child check2"
          src="./../assets/GIAIYEUTHICH.jpg"
          alt=""
          @click="setImage2"
        />
      </div>
      <div class="img-container">
        <img class="img-child" :src="this.reward1.ImgUrl" alt="" />
        <img
          class="img-child check1"
          src="./../assets/GIAINHAT.jpg"
          alt=""
          @click="setImage1"
        />
      </div>
    </div>
  </div>
</template>

<script>
import GroupReward from "@/services/GroupReward.js";
export default {
  data() {
    return {
      flickityOptions: {
        initialIndex: 2,
        prevNextButtons: true,
        wrapAround: true,
        freeScroll: true,
        autoPlay: 1500,
        pauseAutoPlayOnHover: false,
        pageDots: false,
      },
      data: [],
      reward1: null,
      reward2: null,
      reward3: null,
    };
  },
  methods: {
    onTitleChange() {
      let _title = "";
      this.$store.dispatch("title", _title);
    },
    setImage1() {
      this.$anime({
        targets: ".img-container .check1",
        opacity: 0,
        duration: 1000,
        easing: "linear",
        loop: false,
      });
    },
    setImage2() {
      this.$anime({
        targets: ".img-container .check2",
        opacity: 0,
        duration: 1000,
        easing: "linear",
        loop: false,
      });
    },
    setImage3() {
      this.$anime({
        targets: ".img-container .check3",
        opacity: 0,
        duration: 1000,
        easing: "linear",
        loop: false,
      });
    },

    async onDataChange(items) {
      let _data = [];

      items.forEach((item) => {
        let data = item.val();
        _data.push({
          id: data.id,
          name: data.name,
          ImgUrl: data.ImgUrl,
        });
        console.log(data.name);
        switch (data.name) {
          case 1:
            this.reward1 = data;
            break;

          case 2:
            this.reward2 = data;
            break;

          case 3:
            this.reward3 = data;
            break;
        }
      });

      this.data = _data;
    },
  },
  mounted() {
    this.onTitleChange();
    GroupReward.getAll().on("value", this.onDataChange);
  },
};
</script>

<style scoped>
.carousel-cell img {
  max-width: 500px;
}

.container-reward {
  padding: 125px 0 0 0;
}

.container-reward .img-container {
  width: 100%;
  height: 700px;
  position: relative;
  padding: 0 10px;
}

.container-reward .img-container img {
  max-width: 500px;
}

.img-child {
  position: absolute;
  margin-left: auto;
  margin-right: auto;
  left: 0;
  right: 0;
  text-align: center;
}

.set-layout-btn {
  text-align: center;
}
</style>
